import React from 'react';
import './index.css';
import { HelmetProvider, Helmet } from 'react-helmet-async';

const Record = () => {
  return (
    <HelmetProvider>
      <Helmet>
        <title>이상청</title>
        <meta name="description" content="Description goes here." />
      </Helmet>
      <div className="background_split">
        <div className="header">
          <div
            style={{
              width: '30px',
              height: '30px',
              borderRadius: '50%',
              backgroundColor: '#EDF5F0',
            }}
          ></div>
        </div>
        <p className="title1">내년의 나를 위해 남기는</p>
        <p className="title2">하루 날씨 기록</p>
        <div className="wrapper">
          <div className="content_top">
            <p className="date">2022년 7월 12일</p>
            <div className="content_top_left">
              <div
                style={{
                  width: '30px',
                  height: '30px',
                  borderRadius: '50%',
                  backgroundColor: '#54B877',
                }}
              ></div>
              <div
                style={{
                  width: '30px',
                  height: '30px',
                  borderRadius: '50%',
                  backgroundColor: '#54B877',
                }}
              ></div>
            </div>
          </div>
          <hr className="line_horizontal" />
          <div className="content_bottom">
            <div
              style={{ width: '20vw', height: '20vw', backgroundColor: 'grey' }}
            ></div>
            <div className="content_bottom_right">
              <div className="content_bottom_right_top">
                <div
                  style={{
                    width: '40px',
                    height: '40px',
                    borderRadius: '50%',
                    backgroundColor: '#54B877',
                  }}
                ></div>
                <div className="temperature">
                  <p className="temperature_value">20</p>
                  <p className="temperature_sign">°C</p>
                </div>
                <hr className="line_vertical" />
                <p className="temperature_info">최고: 24° / 최저: 25°</p>
                <p className="temperature_info">강수확률: 0%</p>
              </div>
              <textarea className="content_bottom_right_bottom" />
            </div>
          </div>
        </div>
      </div>
    </HelmetProvider>
  );
};

export default Record;
